
  import loading from '@/components/loading/dataLoading';
  import {setTitle} from '@/common/js/common';
  import html2canvas from 'html2canvas';
  import $ from 'jquery';
  //  import html2canvas from '@/common/js/html2canvas';
  import wx from 'weixin-js-sdk';
  import Alert from 'vux/src/components/alert/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
;

  export default{
    data(){
      return{
        loading:true,
        dialog_show:false,
        pic:"",
        position:"",
        time:"",
        name:"",
        sex:"",
        phone:"",
        confirm_show:false,
        content:"",
        dialog_title:"",
        state:0
      }
    },
    components:{
      loading,Alert,Group,XButton
    },
    mounted(){
      setTitle("");
      this.getdata();

    },
    methods:{
      getdata(){
        let self = this;
        if(service_url){
          this.$http.get(service_url+"/o2o/door/wx/door/invite/qrcode?id="+this.$route.query.id).then((data)=>{
            if(data.body.statusCode==="ok"){
              if(data.body.status==="OK"){
                self.pic=data.body.data.pic;
                self.position=data.body.data.position.desc;
                self.time=data.body.data.time.val;
                self.name=data.body.data.name.val;
                self.sex=data.body.data.sex.val;
                self.phone=data.body.data.phone.val;
              }else{
                this.dialog_title="";
                this.content="!";
                this.confirm_show=true;
              }
            }else{
              alert(data.body.message);
            }

          })
        }else{
          this.$http.get("/api/code_share").then((data)=>{
            self.pic=data.body.data.data.pic;
            self.position=data.body.data.data.position.desc;
            self.time=data.body.data.data.time.val;
            self.name=data.body.data.data.name.val;
            self.sex=data.body.data.data.sex.val;
            self.phone=data.body.data.data.phone.val;
            self.state=1;
          })
        }
        setTimeout(this.wxShare,1000);
      },
      wxShare(){
          let self =this;
//        alert("");
        let w = $("#containers").width();
        let h = $("#containers").height();
        var canvas = document.createElement("canvas");
        canvas.width = w * 2;
        canvas.height = h * 2;
        canvas.style.width = w + "px";
        canvas.style.height = h + "px";
        let context = canvas.getContext("2d");
        context.scale(2,2);

        html2canvas($("#containers"), {
          canvas: canvas,
          onrendered: function(canvas) {
            let dataUrl = canvas.toDataURL();
            const items={
              "data": {
                "img": dataUrl, //
              }
            };
            if(service_url){
              self.$http.post(service_url+"/o2o/door/wx/door/share",items).then((data)=>{
                if (data.body.statusCode === "ok") {
                  self.share_url=data.body.data.share_url;
                  wx.config({
                    debug: false, // ,apialertpclogpc
//                        debug : true, // ,apialertpclogpc
                    appId: userProfile.wx.appid, // 
                    timestamp: userProfile.wx.timestamp, // 
                    nonceStr: userProfile.wx.nonceStr, // 
                    signature: userProfile.wx.signature,// 1
                    jsApiList: ['onMenuShareAppMessage']
                    // JSJS2
                  });
                  wx.ready(function () {
                    if (self.share_url) {
//                      wx.hideMenuItems({
//                        menuList: ["menuItem:share:timeline,menuItem:share:weiboApp,menuItem:share:facebook,menuItem:share:QZone"] // menu3
//                      });
                      wx.onMenuShareAppMessage({
                        title: '', // 
                        desc: '', // 
                        link: self.share_url, // 
                        imgUrl: '', // 
                        type: '', // ,musicvideolinklink
                        dataUrl: '', // typemusicvideo
                        success: function () {
                          self.dialog_title="";
                          self.content="";
                          self.confirm_show=true;
                        },
                        cancel: function () {
                          // 
                        }
                      });
                      /*wx.onMenuShareQQ({
                       title: '', // 
                       desc: '', // 
                       link: self.share_url, // 
                       imgUrl: '', // 
                       success: function () {
                       self.dialog_title="";
                       self.content="";
                       self.confirm_show=true;
                       },
                       cancel: function () {
                       // 
                       }
                       });*/
                    }
                  })
                } else {
                  self.dialog_title="";
                  self.content=data.message;
                  self.confirm_show=true;
                }
              })
            }
          }
        });
        document.getElementById("index_loading").style.display="none";
      },
      help(){
        this.dialog_show=true;
      },
      dialog_hide(){
        this.dialog_show=false;
      },
      onShow(){

      },
      onHide(){
        this.$router.push({path:"index"});
      }
    }
  }



